<template>
  <div class="testProject">
    <div class="function">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <el-input
                placeholder="ID"
                icon="search"
                v-model="input">
            </el-input>
          </div>
        </el-col>
        <el-col :span="20">
          <el-button type="primary" @click.native="onNewClick"></el-button>
        </el-col>
      </el-row>
    </div>
    <div id="table">
      <el-table
          :data="tableData"
          stripe
          style="width: 100%">
        <el-table-column
            prop="id"
            label="ID">
        </el-table-column>
        <el-table-column
            prop="step"
            label="">
        </el-table-column>


        <el-table-column
            prop="videoPath"
            label="">
        </el-table-column>

        <el-table-column
            :context="_self"
            inline-template
            label="">
          <span>
            <el-button @click="" type="text" size="small"></el-button>
            <el-button @click="" type="text" size="small"></el-button>
            <el-button @click="onDelClick($index)" type="text" size="small"></el-button>
            <el-button @click="onEditClick($index)" type="text" size="small"></el-button>
          </span>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="dialogTitle" v-model="isDialogShow" size="small">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload
              class="upload-demo"
              action=""
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :before-remove="beforeRemove"
              :multiple="false"
              :limit="1"

              :file-list="fileList">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isDialogShow = false"> </el-button>
        <el-button type="primary" @click="isDialogShow = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import videoData from "../../mock/videoData";

export default {
  data() {
    return {
      input: '',
      dialogTitle: '',
      isDialogShow: false,
      tableData: videoData,
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '120px'

    }

  },
  methods: {
    // 
    onEditClick: function (index) {
      this.dialogTitle = "";
      this.isDialogShow = true;
    },

    // 
    onDelClick: function (index) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        this.tableData.splice(index, 1);
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    onNewClick: function () {
      this.dialogTitle = "";
      this.isDialogShow = true;
    },

    handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      }
  }
}
</script>

<style media="screen">
.function {
  margin-bottom: 10px;
}

.function button {
  float: right;
}
</style>



